.page-footer {
    background: #f7f7f7;
    box-shadow: 0px -5px 40px var(--shadow1);
    .container {
        height: 89px;
        margin: 0 auto;
        display: flex;
        justify-content: space-between;
        align-items: center;
    }
}

.page-footer__logo {
    margin: 0px;
    color: var(--color1);
    font-family: var(--font1);
    font-size: 26px;
    font-weight: 300;
}

.page-footer__copyrights {
    color: var(--color2);
    font-family: var(--font1);
    font-size: 11px;
    font-weight: 400;
}

.page-footer__links {
    width: 68px;
    height: 24px;
    display: flex;
    justify-content: space-between;
}