.articles {
    height: auto;
    background: #f7f7f7;
    padding-top: 105px;
    margin: 0;
    @include desktop {
        height: 1395px;
    }
}

.article-top {
    background: #fff;
    border: 1px solid var(--color);
    margin: 0 auto;
    margin-bottom: 37px;
    border-radius: 5px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    @include mobile {
        width: 100%;
        height: auto;
        flex-direction: column-reverse;
    }
    @include desktop {
        width: 1124px;
        height: 370px;
    }
}

.article-top__content {
    margin-left: 35px;
    position: relative;
    @include mobile {
        width: 80%;
        height: 100%;
    }
    @include desktop {
        width: 416px;
        height: 188px;
        &::after {
            content: "";
            position: absolute;
            width: 327px;
            height: 4px;
            background: var(--color3);
            bottom: 0;
            z-index: 1;
        }
    }
}

.article-top__aside {
    width: 433px;
    height: 294px;
    position: relative;
    margin-right: 71px;
}

.article-top__aside--bg {
    width: 254px;
    height: 254px;
    position: absolute;
    top: 0;
    right: 0;
    &::after {
        content: "";
        width: 180px;
        height: 180px;
        background: var(--color3);
        transform: rotate(45deg);
        position: absolute;
        top: 30px;
    }
    @include mobile {
        &::after {
            left: 95px;
            border-radius: 80px;
        }
    }
    @include desktop {
        &::after {
            left: 45px;
            border-radius: 41px;
        }
    }
}

.article-top__aside--photo {
    width: 386px;
    height: 219px;
    position: absolute;
    bottom: 0;
    background: url(../assets/macbook.png) no-repeat center /cover;
    @include mobile {
        left: 50px;
    }
    @include desktop {
        left: 0;
    }
}

.article-top__title {
    font-family: var(--font2);
    font-size: 36px;
    color: #000;
    font-weight: 400;
    width: 70%;
    margin-bottom: 30px;
    margin-top: 60px;
    @include desktop {
        margin-top: 0;
    }
}

.article-top__text {
    font-family: var(--font1);
    font-size: 16px;
    color: #000;
    font-weight: 400;
    margin-bottom: 80px;
    line-height: 1.5em;
    @include desktop {
        margin-bottom: 0;
        line-height: 1em;
    }
}

.article-medium {
    background: #fff;
    border: 1px solid var(--color);
    margin: 0 auto;
    margin-bottom: 37px;
    border-radius: 5px;
