        width: 100%;
        height: auto;
    }
    @include desktop {
        width: 440px;
        height: 370px;
    }
}

.article-bottom__trumpet--bg {
    position: relative;
    top: 0;
    left: 0;
    background: var(--color4);
    border: 1px solid var(--color);
    border-radius: 5px;
    @include mobile {
        width: 100%;
        height: 207px;
    }
    @include desktop {
        width: 440px;
        height: 370px;
    }
}

.article-bottom__trumpet--photo {
    position: absolute;
    width: 295px;
    height: 207px;
    background: url(../assets/trumpet.png) no-repeat center/contain;
    @include mobile {
        bottom: -10%;
        left: 0;
    }
    @include desktop {
        top: 98px;
        left: 62px;
    }
}