.form {
    width: 896px;
    height: 376px;
    margin: 0 auto;
    padding-bottom: 39px;
    .container {
        display: flex;
        justify-content: space-between;
    }
}

.form__contact-details {
    width: 350px;
    height: 376px;
}

.form__contact-detail--title {
    font-family: var(--font2);
    color: #000;
    font-weight: 400;
    font-size: 49px;
    margin: 0;
}

.form__contact-detail--text {
    font-family: var(--font1);
    color: #000;
    font-weight: 400;
    font-size: 16px;
    margin-top: 22px;
    margin-bottom: 42px;
}

.form__contact-detail--list {
    display: flex;
    justify-content: space-between;
    align-items: center;
    list-style: none;
    li {
        &:first-child {
            &::before {
                content: "";
                width: 27px;
                height: 27px;
                position: absolute;
                left: -35px;
                background-image: url(../assets/mailicon.svg);
            }
        }
        &:last-child {
            &::before {
                content: "";
                width: 27px;
                height: 27px;
                left: -35px;
                position: absolute;
                background-image: url(../assets/phoneicon.svg);
            }
        }
    }
}

.form__contact-detail--item {
    position: relative;
    line-height: 22px;
    margin: 0;
    padding: 0;
    font-family: var(--font1);
    color: var(--color2);
    font-weight: 700;
    font-size: 16px;
}

.form__user-details {
    width: 440px;
    height: 376px;
}

.form__user-details--form {
    display: flex;
    flex-direction: column;
}

.form__user-details--form-inputs {
    width: 440px;
    height: 157px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    padding: 0;
    margin-top: 4px;
    margin-bottom: 30px;
}

.form__user-details--name {
    display: flex;
    flex-direction: column;
}

.form__user-details--email {
    display: flex;
    flex-direction: column;
}

.form__user-details--name-label {
    font-family: var(--font1);
    color: var(--color1);
    font-weight: 700;
    font-size: 11px;
    margin: 0;
    padding: 0;
    text-transform: uppercase;
    letter-spacing: 3.28px;
}

.form__user-details--email-label {
    font-family: var(--font1);
    color: var(--color1);
    font-weight: 700;
    font-size: 11px;
    margin: 0;
    padding: 0;
    text-transform: uppercase;
    letter-spacing: 3.28px;
}

.form__user-details--name-input {
    width: 440px;
    height: 43px;
    border: none;
    border-bottom: 1px solid #000;
}

.form__user-details--email-input {
    width: 440px;
    height: 43px;
    border: none;
    border-bottom: 1px solid #000;
}

.form__user-details--checkbox {
    display: flex;
    margin-bottom: 20px;
}

.form__user-details--checkbox-input {
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
    outline: none;
    width: 24px;
    height: 24px;
    display: inline-block;
    border-radius: 4px;
    border: 1px solid #95989A;
    background: #fff;
    position: relative;
    flex-shrink: 0;
    margin-right: 10px;
    cursor: pointer;
    &:checked {
        background: url(../assets/checkmark.svg) no-repeat center;
    }
}

.form__user-details--checkbox-label {
    font-family: var(--font1);
    color: #707070;
    font-weight: 400;
    font-size: 12px;
    margin: 0;
    padding: 0;
    line-height: 17px;
    cursor: pointer;
}

.form__user-details--btn {
    align-self: flex-end;
}

.form__user-details--btn-submit {
    width: 226px;
    height: 57px;
    border: none;
    border-radius: 35px;
    background: var(--color1);
    box-shadow: 5px 10px 14px var(--shadow2);
    color: #fff;
    font-family: var(--font1);
    font-size: 13px;
    font-weight: 700;
    cursor: pointer;
    outline: none;
}